* {
  margin: 0;
  text-decoration: none;
  box-sizing: border-box;
 // font-family: 'Raleway', sans-serif;
}
html {
  scroll-behavior: smooth;
  // scroll-margin-top: 20px;
  scroll-padding-top: 60px;
}
.navbar {
  //background-color: #ffffff;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.1);

   background: rgb(0, 200, 255);
   background: white;
  z-index: 9999;

  height: 60px;
  display: flex;
  /* justify-content: space-evenly; */
  align-items: center;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
}
.navbar a {
  color: rgb(24, 23, 23);
  font-weight: 600;
  padding: 14px 30px;
  min-width: 150px;
  text-align: center;
}
#resume-button-1 {
   background: none;
  border: none;
  font-size: 20px;
  color: white;
 color: rgb(40, 38, 38);
  font-weight: 600;
}
.menu-btn {
  display: none;
}
.navbar a:hover ,.navbar a:hover #resume-button-1 , .navbar a.link-active {
  // background-image: linear-gradient(-45deg,blue,skyblue);
  // background-image: linear-gradient(-45deg, rgb(78, 78, 163), rgb(50, 56, 58));
  // background-image: linear-gradient(-45deg,rgb(109, 109, 109),rgb(0, 0, 0));
  background-size: 200%;
  font-size: 20px;
  border-radius: 4px;
  color: blue;
  // padding: 1px;
}
.navbar h1 {

  font-weight: 600;
  margin: 0 20px;

  
  background-size: 150%;
  padding: 0 20px;
  border-radius: 10px 20px 30px 50px;

}

.links {
  display: flex;
  align-items: center;
  margin: auto;
  font-size: 20px;
  gap: 10px;
}

.home-img {
  /* height: 200px; */
}
#about,
#contact,
#home,
#skills,
#projects {
  /*  */
  padding-top: 60px;
  // scroll-padding-top: 20px;
}

.react-img {
  width: 400px;
}

